/* eslint-disable jsx-a11y/accessible-emoji */
import React, { useState } from 'react';
import './App.scss';
import classNames from 'classnames';

import usersFromServer from './api/users';
import categoriesFromServer from './api/categories';
import productsFromServer from './api/products';

const products = productsFromServer.map((product) => {
  const category = categoriesFromServer
    .find(cat => cat.id === product.categoryId);

  const user = usersFromServer
    .find(u => u.id === category.ownerId);

  return {
    category,
    product,
    user,
  };
});

export const App = () => {
  const [selectedUserId, setSelectedUserId] = useState(null);
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [searchQuery, setSearchQuery] = useState('');

  const filteredProducts = products
    .filter((item) => {
      const { user, category, product } = item;

      if (!selectedUserId && !selectedCategory && !searchQuery.trim()) {
        return true;
      }

      if (searchQuery.trim() && !product.name
        .toLowerCase()
        .includes(searchQuery.trim().toLocaleLowerCase())) {
        return false;
      }

      if (selectedUserId && user.id !== selectedUserId) {
        return false;
      }

      if (selectedCategory && category.id !== selectedCategory) {
        return false;
      }

      return true;
    });

  return (
    <div className="section">
      <div className="container">
        <h1 className="title">Product Categories</h1>

        <div className="block">
          <nav className="panel">
            <p className="panel-heading">Filters</p>

            <p className="panel-tabs has-text-weight-bold">
              <button
                data-cy="FilterAllUsers"
                type="button"
                onClick={() => setSelectedUserId(null)}
                href="#"
                className={classNames({
                  'is-active': selectedUserId === null,
                })}
              >
                All
              </button>
              {usersFromServer.map(user => (
                <button
                  key={user.id}
                  type="button"
                  onClick={() => setSelectedUserId(user.id)}
                  className={classNames({
                    'is-active': selectedUserId === user.id,
                  })}
                >
                  {user.name}

                </button>
              ))}
            </p>

            <div className="panel-block">
              <p className="control has-icons-left has-icons-right">
                <input
                  data-cy="SearchField"
                  type="text"
                  className="input"
                  placeholder="Search"
                  value={searchQuery}
                  onChange={e => setSearchQuery(e.target.value)}
                />

                <span className="icon is-left">
                  <i className="fas fa-search" aria-hidden="true" />
                </span>
                {searchQuery && (
                <span className="icon is-right">
                  {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
                  <button
                    data-cy="ClearButton"
                    className="delete"
                    type="button"
                    onClick={() => setSearchQuery('')}
                  />
                </span>
                )}
              </p>
            </div>

            <div className="panel-block is-flex-wrap-wrap">
              <a
                href="#/"
                data-cy="AllCategories"
                className="button is-success mr-6 is-outlined"
                onClick={() => setSelectedCategory(null)}
              >
                All
              </a>
              {products.map((pr) => {
                const { category } = pr;

                return (
                  <a
                    key={category.id}
                    data-cy="Category"
                    className="button mr-2 my-1 is-info"
                    href="#/"
                    onClick={() => setSelectedCategory(category.id)}
                  >
                    {category.title}
                  </a>
                );
              })}

            </div>

            <div className="panel-block">
              <a
                data-cy="ResetAllButton"
                href="#/"
                className="button is-link is-outlined is-fullwidth"
              >
                Reset all filters
              </a>
            </div>
          </nav>
        </div>

        <div className="box table-container">
          <p data-cy="NoMatchingMessage">
            No products matching selected criteria
          </p>

          <table
            data-cy="ProductTable"
            className="table is-striped is-narrow is-fullwidth"
          >
            <thead>
              <tr>
                <th>
                  <span className="is-flex is-flex-wrap-nowrap">
                    ID

                    <a href="#/">
                      <span className="icon">
                        <i data-cy="SortIcon" className="fas fa-sort" />
                      </span>
                    </a>
                  </span>
                </th>

                <th>
                  <span className="is-flex is-flex-wrap-nowrap">
                    Product

                    <a href="#/">
                      <span className="icon">
                        <i data-cy="SortIcon" className="fas fa-sort-down" />
                      </span>
                    </a>
                  </span>
                </th>

                <th>
                  <span className="is-flex is-flex-wrap-nowrap">
                    Category

                    <a href="#/">
                      <span className="icon">
                        <i data-cy="SortIcon" className="fas fa-sort-up" />
                      </span>
                    </a>
                  </span>
                </th>

                <th>
                  <span className="is-flex is-flex-wrap-nowrap">
                    User

                    <a href="#/">
                      <span className="icon">
                        <i data-cy="SortIcon" className="fas fa-sort" />
                      </span>
                    </a>
                  </span>
                </th>
              </tr>
            </thead>

            <tbody>
              {filteredProducts.map((prod) => {
                const { user, product, category } = prod;
                const isFemale = user.sex === 'f';

                return (
                  <tr
                    key={product.id}
                    data-cy="Product"
                  >
                    <td className="has-text-weight-bold" data-cy="ProductId">
                      {product.id}
                    </td>

                    <td data-cy="ProductName">{product.name}</td>
                    <td data-cy="ProductCategory">
                      🍺 -
                      {category.title}
                    </td>

                    <td
                      data-cy="ProductUser"
                      className={classNames(`has-text-link`, { 'has-text-danger': isFemale })}
                    >
                      {user.name}
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};
